import {createPageElement} from "./module.js";
import {addToLS} from "./module.js";
import {getFromLS} from "./module.js";

const root = document.querySelector("#root");

//–°–æ–∑–¥–∞—ë–º —ç–ª–µ–º–µ–Ω—Ç—ã —Å—Ç—Ä–∞–Ω–∏—Ü—ã WB

function createWBElements(){
    const mainContainer = createPageElement("div", "wb-container", root, "", "", ""); //–≥–ª–∞–≤–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä
    const headerWB = createPageElement("div", "wb-header", mainContainer, "", "", ""); //—Ö–µ–∞–¥–µ—Ä
    const logoWB = createPageElement("img", "wb-header__wblogo", headerWB, "", "", ""); //–ª–æ–≥–æ—Ç–∏–ø
    const searchWB = createPageElement("input", "wb-header__search", headerWB, "", "","–ü–æ–∏—Å–∫..."); //–ø–æ–ª–µ –ø–æ–∏—Å–∫–∞

        // –Ω–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –ø–æ–∏—Å–∫ –ø–æ —Å—Ç—Ä–∞–Ω–∏—Ü–µ

        searchWB.addEventListener("input", () => {
            let searchValue = searchWB.value.toLowerCase().trim();
            let cardsList = document.querySelectorAll(".card-container");
            Array.from(cardsList).forEach((elem) => {
                if (!elem.lastChild.lastChild.textContent.toLocaleLowerCase().includes(searchValue)) {
                    elem.style.display = "none";
                } else {
                    elem.style.display = "flex";
                }
            })
        })

    const cartWB = createPageElement("img", "wb-header__cart", headerWB, "", "", ""); //–∫–æ—Ä–∑–∏–Ω–∞

    // –Ω–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –æ—Ç–∫—Ä—ã—Ç–∏–µ –∫–æ—Ä–∑–∏–Ω—ã

    cartWB.addEventListener("click", () => {   
        return modalCart.style.display = "flex"
    })
    const modalCart = createPageElement("div", "wb-header-modal", mainContainer, "", "", ""); // –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    const modalHeader = createPageElement("header", "wb-header-modal-header", modalCart, "", "", ""); // header –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    const modalContent = createPageElement("div", "wb-header-modal-content", modalCart, "", "", ""); // —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    const modalClearBtn = createPageElement("button", "wb-header-modal__clear-btn", modalHeader, "", "–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É", ""); // –∫–Ω–æ–ø–∫–∞ –æ—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É
        modalClearBtn.addEventListener("click", () => {
            localStorage.clear();
            modalContent.innerHTML = "";
            modalSumCalc.innerHTML = "";
        })
    const modalCloseBtn = createPageElement("button", "wb-header-modal__close-btn", modalHeader, "", "X", ""); // –∫–Ω–æ–ø–∫–∞ –∑–∞–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    const modalSumZone = createPageElement("div", "wb-header-modal-footer", modalCart,"", "", ""); // –§—É—Ç—Ç–µ—Ä —Å —Å—É–º–º–æ–π –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤
    const modalSumText = createPageElement("h3", "wb-header-modal-footer__text", modalSumZone,"", "–°—É–º–º–∞ –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤:", ""); //–ü—Ä–æ—Å—Ç–æ –∑–∞–≥–æ–ª–æ–≤–æ–∫ "–°—É–º–º–∞"
    const modalSumCalc = createPageElement("h3", "wb-header-modal-footer__sum", modalSumZone,"", "", ""); 
      
        //–Ω–∞—Å—Ç—Ä–∏–≤–∞–µ–º –∑–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞

        modalCloseBtn.addEventListener("click", () => {
            if (modalCart.style.display = "block") {
                return modalCart.style.display = "none"
            }
        })
    const sliderWB = createPageElement("div", "wb-container__slider", mainContainer, "", "", "");
        sliderWB.innerHTML = 
        `<div id="carouselExampleIndicators" class="carousel slide">
            <div class="carousel-indicators">
                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
            </div>
            <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img class="carousel__img" src="https://loremflickr.com/640/480/food?random=1" alt="logo">
                    </div>
                    <div class="carousel-item">
                        <img class="carousel__img" src="https://loremflickr.com/640/480/food?random=2" alt="logo">
                    </div>
                    <div class="carousel-item">
                        <img class="carousel__img" src="https://loremflickr.com/640/480/food?random=3" alt="logo">
                    </div>
            </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">–ü—Ä–µ–¥—ã–¥—É—â–∏–π</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">–°–ª–µ–¥—É—é—â–∏–π</span>
                </button>
            </div>
        </div>`
    const hitsWB = createPageElement("h1", "wb-container__hits", mainContainer, "", "–•–∏—Ç—ã –ø—Ä–æ–¥–∞–∂:", ""); //–ó–∞–≥–æ–ª–æ–≤–æ–∫ "–•–∏—Ç—ã –ø—Ä–æ–¥–∞–∂"
    const cardsWB = createPageElement("div", "wb-container__cards", mainContainer, "", "", ""); //–û–±–ª–∞—Å—Ç—å —Å –∫–∞—Ä—Ç–æ—á–∫–∞–º–∏ —Ç–æ–≤–∞—Ä–∞"

    // –î–æ–±–∞–≤–ª—è–µ–º –∞—Ç—Ç—Ä–∏–±—É—Ç—ã —ç–ª–µ–º–µ–Ω—Ç–∞–º

    logoWB.setAttribute("src", "https://static.tildacdn.com/tild3134-3336-4530-b166-653865636661/i_1.png");
    cartWB.setAttribute("src", "https://avatanplus.com/files/resources/original/5ed9e94e590e117283375a1d.png");
};
createWBElements();

// —Ñ—É–Ω–∫—Ü–∏—è –ø–æ–ª—É—á–µ–Ω–∏—è mockap 

async function getData() {
    let response = await fetch('https://65cb5414efec34d9ed87430b.mockapi.io/wb1/WB/');
    let productCards = await response.json();
    return productCards;
};
getData().then(item => {
    item.forEach(element => {
        createWBCard(element);
    });
})

function createWBCard(obj){
    let discount = Math.floor(5 + Math.random() * 40); // –†–∞–Ω–¥–æ–º–Ω—ã–π —Ä–∞–∑–º–µ—Ä —Å–∫–∏–¥–∫–∏
    let oldPrice = Math.round(obj.price); //–í—ã—á–ª–µ–Ω—è–µ–º —Å—Ç–∞—Ä—É—é (–∑–∞—á—ë—Ä–∫–Ω—É—Ç—É—é —Ü–µ–Ω—É) –∏–∑ –æ–±—ä–µ–∫—Ç–∞
    let newPrice = Math.round(oldPrice - oldPrice * (discount / 100)); // –í—ã—á–∏—Å–ª—è–µ–º –Ω–æ–≤—É—é —Ü–µ–Ω—É
    const cardsWBZone = document.querySelector(".wb-container__cards");
    const cardContainer = createPageElement("div", "card-container", cardsWBZone, "", "", ""); //–ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–∞—Ä—Ç–æ—á–∫–∏ —Ç–æ–≤–∞—Ä–∞
    const cardPictureZone = createPageElement("div", "card-box", cardContainer, "", "", ""); //–û–±–ª–∞—Å—Ç—å —Å –∫–∞—Ä—Ç–∏–Ω–∫–æ–π, —Å–∫–∏–¥–∫–æ–π –∏ –∫–Ω–æ–ø–∫–æ–π "–î–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É"
        
        //–ü–æ–∫–∞–∑—ã–≤–∞–µ–º –∫–Ω–æ–ø–∫—É "–ë—ã—Å—Ç—Ä—ã–π –ø—Ä–æ—Å–º–æ—Ç—Ä" –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ –Ω–∞ –∫–∞—Ä—Ç–æ—á–∫—É —Ç–æ–≤–∞—Ä–∞

        cardPictureZone.addEventListener('mouseover', function(){
        cardPictureQuickBtn.style.display = "block";
        })
        cardPictureZone.addEventListener('mouseout', function(){
        cardPictureQuickBtn.style.display = "none";
        })

    const cardPicture = createPageElement("div", "card-box__image", cardPictureZone, "", "", ""); //–ö–∞—Ä—Ç–∏–Ω–∫–∞ —Ç–æ–≤–∞—Ä–∞
    const cardPictureQuickBtn = createPageElement("button", "card-box__quickview", cardPictureZone, "", "–ë—ã—Å—Ç—Ä—ã–π –ø—Ä–æ—Å–º–æ—Ç—Ä", ""); //–∫–Ω–æ–ø–∫–∞ "–ë—ã—Å—Ç—Ä—ã–π –ø—Ä–æ—Å–º–æ—Ç—Ä"
    const cardDiscount = createPageElement("h5", "card-box__discount", cardPictureZone, "","- " + discount + "%", ""); //–†–∞–∑–º–µ—Ä —Å–∫–∏–¥–∫–∏ –≤ –ø—Ä–æ—Ü–µ–Ω—Ç–∞—Ö
    const cardBuyBtn = createPageElement("img", "card-box__buybtn", cardPictureZone, "", "", ""); //–ö–Ω–æ–ø–∫–∞ "–î–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É"
        
    // –¥–æ–±–∞–≤–ª—è–µ–º –∫–∞—Ä—Ç—É –≤ LS

        cardBuyBtn.addEventListener("click", () => {
            addToLS(obj.id, obj.title, newPrice);
            getFromLS(document.querySelector(".wb-header-modal-content"));
        })

    const cardInfoZone =createPageElement("div", "card-infozone", cardContainer, "", "", ""); //–û–±–ª–∞—Å—Ç—å —Å —Ü–µ–Ω–æ–π, —Å—Ç–∞—Ä–æ–π —Ü–µ–Ω–æ–π –∏ –Ω–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ–º —Ç–æ–≤–∞—Ä–∞
    const cardPrice = createPageElement("h4", "card-infozone__price", cardInfoZone, "",  newPrice + "$", ""); //–¶–µ–Ω–∞ —Ç–æ–≤–∞—Ä–∞
    const cardPriceCancel = createPageElement("h4", "card-infozone__priceCancel", cardInfoZone, "",  oldPrice + "$", ""); //–ë—ã–≤—à–∞—è —Ü–µ–Ω–∞ —Ç–æ–≤–∞—Ä–∞
    const cardDiscription = createPageElement("h4", "card-infozone__discription", cardInfoZone, "", obj.title, ""); //–ù–∞–∑–≤–∞–Ω–∏–µ —Ç–æ–≤–∞—Ä–∞

    // –î–æ–±–∞–≤–ª—è–µ–º –∞—Ç—Ç—Ä–∏–±—É—Ç—ã —ç–ª–µ–º–µ–Ω—Ç–∞–º

    cardPicture.style.backgroundImage = `url('${obj.picture}?=random${obj.id}')`;
    cardBuyBtn.setAttribute("src", "https://www.expresselectrical.co.uk/imagecache/33034493-5989-403c-99d4-a9570075c59a/Order-Information_592x591.png");
    modalPictureZone.style.backgroundImage = cardPicture.style.backgroundImage; //–≤—Å—Ç–∞–≤–ª—è–µ–º —É–≤–µ–ª–∏—á–µ–Ω–Ω—É—é –∫–∞—Ä—Ç–∏–Ω–∫—É. –ü–†–û–ë–õ–ï–ú–ê! –≤—Å—Ç–∞–≤–ª—è–µ—Ç—Å—è —Ä–∞–Ω–¥–æ–º–Ω–∞—è –∫–∞—Ä—Ç–∏–Ω–∫–∞!

    //–û—Ç–∫—Ä—ã—Ç–∏–µ –æ–∫–Ω–∞ "–ë—ã—Å—Ç—Ä–æ–≥–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞"

    cardPictureQuickBtn.addEventListener("click", () => {
        modalPictureWindow.style.display = "block";
    })

    //–ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ "–ë—ã—Å—Ç—Ä–æ–≥–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞"

    modalPictureClose.addEventListener("click", () => {
    if (modalPictureWindow.style.display = "block") {
        return modalPictureWindow.style.display = "none"
    }
    })
}

// –°–æ–∑–¥–∞—ë–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ "–ë—ã—Å—Ç—Ä–æ–≥–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞"

const selectMain = document.querySelector(".wb-container");
const modalPictureWindow = createPageElement("div", "modalPicture-window", selectMain, "", "", ""); //–≥–ª–∞–≤–Ω–æ–µ –æ–∫–Ω–æ "–ë—ã—Å—Ç—Ä–æ–≥–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞"
const modalPictureClose = createPageElement("button", "modalPicture-windows__closebtn", modalPictureWindow, "", "–ó–∞–∫—Ä—ã—Ç—å", ""); //–∫–Ω–æ–ø–∫–∞ "–ó–∞–∫—Ä—ã—Ç—å" –æ–∫–Ω–∞ "–ë—ã—Å—Ç—Ä–æ–≥–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞"
const modalPictureZone = createPageElement("div", "modalPictureZone", modalPictureWindow, "", "", ""); //–û–±–ª–∞—Å—Ç—å —Å —É–≤–µ–ª–∏—á–µ–Ω–Ω–æ–π –∫–∞—Ä—Ç–∏–Ω–∫–æ–π
